import React, { Component } from 'react';
import { connect } from "react-redux";
import moment from 'moment'
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { GET_POST_TYPE_NAME, GET_NETWORK_ICON, GET_MEDIA_NAME, MEDIA_REPLACE_URL, FIND_COLLECTION_NAME } from '../../../../../Services/Utils/utils';
import { postAddTag } from '../../../../../Services/Actions/tagsActions'
import { postPostUpdateAction } from '../../../../../Services/Actions/assetsActions'
//import ModalTagProducts from './ModalTagProducts';



class AssetsModalAside extends Component {
    constructor(props) {
        super(props);
        this.state = {
            //tagString:['#macdonald', 'Restaurant', 'Burger', '#macdonaldUSA', 'California', '#starbucks', 'Restaurant'],
            descriptionEditable: false,
            addTagButton: true,
            //tagInputValue : ''
            tagString: [],
            newTag: "",
            listTags: [],
            selectedOption: null,
            content: "",
            changesStatus: false
        };

        this.editorEnable = React.createRef();
        this.editorEnableButton = React.createRef();
    }

    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        tagString.splice(index, 1)
        this.setState({ tagString, changesStatus: true })

    }

    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })

            console.log("componentWillMount", this.props)
        }

    }

    addTagButtonValue = () => {
        this.setState({
            addTagButton: false
        })
    }

    // tagInputValueFun = (value) => {
    //     console.log('value', value)
    //     this.setState({
    //         tagInputValue : value
    //     })
    // }

    descriptionToggle() {
        this.setState({
            descriptionEditable: true
        })
    }

    componentDidMount() {
        document.addEventListener('click', this.handleOutsideClick);
        const { modalAssetData } = this.props
        if (modalAssetData && Object.keys(modalAssetData).length > 0) {
            if (modalAssetData.tags && String(modalAssetData.tags).length > 0) this.setState({ tagString: String(modalAssetData.tags).split(","), content: modalAssetData.content })
            else this.setState({ tagString: [], content: modalAssetData.content })
        }
    }

    handleOutsideClick = (event) => {
        if (this.editorEnable && this.editorEnable.current && !this.editorEnable.current.contains(event.target) && (this.editorEnableButton && !this.editorEnableButton.current.contains(event.target)) && this.editorEnableButton && !this.editorEnableButton.current.contains(event.target) !== null) {
            this.setState({
                descriptionEditable: false
            })
        } else {
            if (this.editorEnable && this.editorEnable.current && (!this.editorEnable.current.contains(event.target) && this.editorEnable.current.contains(event.target) !== null)) {
                this.setState({
                    descriptionEditable: true
                })
            }
        }
    }

    onClickNewAddTag = event => {
        const { newTag, tagString } = this.state;
        const { modalAssetData } = this.props

        if (event.key === 'Enter' || event.keyCode === 13) {
            if (newTag && String(newTag).length >= 3) {
                tagString.push(newTag)
                this.setState({ tagString, newTag: "", selectedOption: null, changesStatus: true, addTagButton: true })
            }
            else {

            }
        }
    }

    addNewTag = event => this.setState({ newTag: event.target.value, changesStatus: true })

    onClickToSave = event => {
        const { tagString } = this.state;
        const { modalAssetData } = this.props;
        if (tagString && tagString.length > 0) {
            this.props.postAddTag({
                "postId": String(modalAssetData.id),
                "tags": tagString.join(",")
            })
        }

    }

    setNewTags = (selectedOption) => {
        console.log(`Option selected:`, selectedOption)
        if (selectedOption && selectedOption.value) this.setState({ newTag: selectedOption.value, selectedOption })
        else this.setState({ newTag: "", selectedOption: null })


    };

    onChangeContent = event => {
        this.setState({ content: event.target.value, changesStatus: true })
    }
    onChangeTag = event => {
        console.log("newTag, tagString", event.target.value)
        this.setState({ newTag: event.target.value })
    }
    onSaveChanges = event => {
        const { modalAssetData } = this.props;
        const { tagString, content } = this.state;
        if (tagString && tagString.length > 0 && content) {
            const data = {
                "action": "4",
                "source": modalAssetData.wallId,
                "destination": modalAssetData.wallId,
                "postId": String(modalAssetData.id),
                "postFile": modalAssetData.postFile,
                "tags": tagString.join(","),
                "content": content
            }

            this.props.postPostUpdate(data, () => {
                this.setState({ descriptionEditable: false, addTagButton: true, changesStatus: false })
                //this.props.editMediaCallback(false)
            })
        }
    }

    render() {
        const { descriptionEditable, addTagButton, tagString, content, changesStatus, newTag } = this.state
        const { modalAssetData, networkData, collections, isTrash } = this.props

        const socialNetwork = networkData && networkData.length > 0 ? networkData.filter((network) => network.id == modalAssetData.networkId) : []
        //const collectionName = collections && collections.length > 0 ? collections.filter((cItem) => cItem.id == modalAssetData.wallId) : []
        const collectionName = collections && collections.length > 0 ? FIND_COLLECTION_NAME(modalAssetData.wallId, collections) : []


        return <div className="full_modal_sidebar position-relative">
            <PerfectScrollbar>
                <div className="full_modal_sidebar_inner position-relative">
                    <h3 className="border-bottom mb-xxl-3 mb-2 pb-xxl-3 pb-2 fs-7 pt-1">
                        <span className="text-uppercase fw-medium">General Info</span>
                    </h3>
                    <table className="table table-borderless">
                        <tbody>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">File Name</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{modalAssetData.name}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">File Extension</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{modalAssetData.ext}</span></td>
                            </tr>

                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Type</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{GET_POST_TYPE_NAME(modalAssetData.type)}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Collection</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{collectionName ? collectionName : "--"}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Dimensions</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{modalAssetData.mediaWidth}X{modalAssetData.mediaHeight}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Size</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{modalAssetData.size}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Created</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{moment(modalAssetData.created).format("DD MMM YYYY hh:mmA")}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Modified</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className="text-dark text-break">{moment(modalAssetData.modified).format("DD MMM YYYY hh:mmA")}</span></td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Source</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75">
                                    {socialNetwork && socialNetwork.length > 0 ?
                                        <div className="d-flex align-items-center">
                                            <span className="me-2 d-flex">
                                                <img className="img-fluid" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(socialNetwork[0].id)}.svg`)} height={18} width={18} alt={socialNetwork[0].name} />
                                            </span>
                                            <span className="text-dark">{socialNetwork[0].name}</span>
                                        </div>
                                        : null}
                                </td>
                            </tr>
                            <tr>
                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Posted By</span></td>
                                <td className="py-xxl-2 py-1 px-0 w-75">
                                    <div className="d-flex align-items-center mr-2">
                                        <div className="symbol symbol-34 rounded-circle flex-shrink-0 me-2 overflow-hidden bg-light">
                                            <img className="object-center-cover" src={MEDIA_REPLACE_URL(modalAssetData.picture)} height={40} width={40} alt={GET_MEDIA_NAME(modalAssetData.picture)} />
                                            <link rel="preload" fetchpriority="high" as="image" href={modalAssetData.picture} type="image/webp"></link>
                                        </div>
                                        <div>
                                            <p className="text-dark mb-0"><strong className="text-break">{modalAssetData.name}</strong></p>
                                            <small className="mb-0 text-muted text-break">@{modalAssetData.username}</small>
                                        </div>
                                    </div>
                                </td>
                            </tr>

                            {
                                modalAssetData.ugcRights ? <>
                                    {modalAssetData.ugcRights.status == 2 ? <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Rights</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className="d-flex align-items-center flex-wrap">
                                                <span className="svg-icon me-1 text-success">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24">
                                                        <path d="M0,0H24V24H0Z" fill="none" />
                                                        <path d="M10.112,23.524h-.006a2.092,2.092,0,0,1-.769-.154A14.925,14.925,0,0,1,0,9.472V5.415A2.041,2.041,0,0,1,1.283,3.5L9.324.159A2.026,2.026,0,0,1,10.114,0h.006a1.984,1.984,0,0,1,.785.162L18.944,3.5a2.034,2.034,0,0,1,1.279,1.913V9.476a14.764,14.764,0,0,1-2.541,8.375,14.8,14.8,0,0,1-6.8,5.52A2.034,2.034,0,0,1,10.112,23.524ZM7.1,10.741A1.1,1.1,0,0,0,6.395,11a1.121,1.121,0,0,0-.391.76,1.1,1.1,0,0,0,.266.811L8.608,14.9a1.118,1.118,0,0,0,.779.319H9.4a1.091,1.091,0,0,0,.772-.319l4.169-4.175a.965.965,0,0,0,.139-.138,1.105,1.105,0,0,0-.847-1.813,1.1,1.1,0,0,0-.832.38l-3.41,3.406L7.808,11A1.1,1.1,0,0,0,7.1,10.741Z" transform="translate(1.888 0.238)" fill="currentColor" />
                                                    </svg>
                                                </span>
                                                <span className="text-success">Request Approved</span>
                                            </div>
                                        </td>
                                    </tr> : modalAssetData.ugcRights.status == 1 ? <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Rights</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className="d-flex align-items-center flex-wrap">
                                                <span className="svg-icon me-1 text-warning">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24">
                                                        <path d="M0,0H24V24H0Z" fill="none" />
                                                        <path d="M-937.889-504.477h-.006a2.082,2.082,0,0,1-.769-.154,14.856,14.856,0,0,1-6.8-5.517A14.833,14.833,0,0,1-948-518.528v-4.058a2.05,2.05,0,0,1,1.283-1.916l8.041-3.34a2.034,2.034,0,0,1,.79-.159,2,2,0,0,1,.791.162l8.039,3.338a2.046,2.046,0,0,1,1.279,1.914v4.062a14.835,14.835,0,0,1-2.541,8.375,14.857,14.857,0,0,1-6.8,5.519A2.027,2.027,0,0,1-937.889-504.477Zm-.046-16.137a1.176,1.176,0,0,0-1.166,1.169v3.866a1.177,1.177,0,0,0,.348.811l2.553,2.559.008,0a1.148,1.148,0,0,0,.812.333h.007a1.16,1.16,0,0,0,.819-.341,1.148,1.148,0,0,0,.341-.822,1.162,1.162,0,0,0-.341-.821l-2.209-2.209v-3.381a1.176,1.176,0,0,0-1.171-1.167Z" transform="translate(950.205 528.238)" fill="currentColor" />
                                                    </svg>
                                                </span>
                                                <span className="text-warning">Request Pending</span>
                                            </div>
                                        </td>
                                    </tr> : modalAssetData.ugcRights.status == 3 ? <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Rights</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className="d-flex align-items-center flex-wrap">
                                                <span className="svg-icon me-1 text-danger">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24">
                                                        <path d="M0,0H24V24H0Z" fill="none" />
                                                        <path d="M10.112,23.524h-.006a2.1,2.1,0,0,1-.769-.154A14.925,14.925,0,0,1,0,9.472V5.415A2.041,2.041,0,0,1,1.283,3.5L9.324.159A2.033,2.033,0,0,1,10.114,0h.006a1.99,1.99,0,0,1,.785.162L18.944,3.5a2.034,2.034,0,0,1,1.279,1.913V9.476a14.765,14.765,0,0,1-2.541,8.374,14.8,14.8,0,0,1-6.8,5.52A2.041,2.041,0,0,1,10.112,23.524ZM6.68,7.458a1.07,1.07,0,0,0-.239.029.984.984,0,0,0-.429,1.677L8.72,11.875,6.012,14.583a.986.986,0,0,0,.429,1.679l.008,0a.883.883,0,0,0,.2.023h.029a1,1,0,0,0,.722-.313l2.713-2.713,2.711,2.711a1,1,0,0,0,.958.286.986.986,0,0,0,.43-1.678L11.5,11.875l2.709-2.711a.985.985,0,0,0-.43-1.677,1,1,0,0,0-.958.284l-2.713,2.713L7.4,7.772A.985.985,0,0,0,6.68,7.458Z" transform="translate(2 0.238)" fill="currentColor" />
                                                    </svg>
                                                </span>
                                                <span className="text-danger">Request Rejected</span>
                                            </div>
                                        </td>
                                    </tr> : null}
                                </> : null
                            }
                        </tbody>
                    </table>

                    {/* <ModalTagProducts /> */}

                    <h3 className="border-bottom mb-xxl-3 mb-2 pb-xxl-3 pb-2 fs-7 pt-2">
                        <span className="text-uppercase fw-medium">Tag</span>
                    </h3>



                    {tagString && tagString.length > 0 ? <div className="tags-group d-flex align-items-center flex-wrap">
                        {
                            tagString.map((tag, removeIndex) => {
                                return <div className="block-center badge bg-secondary text-gray-800 rounded-pill border me-2 mb-2">
                                    {tag}
                                    <button className="btn p-0 ms-1" onClick={this.onClickToRemoveTag(removeIndex)}>
                                        <span className="svg-icon me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M12 10.586l4.95-4.95 1.414 1.414-4.95 4.95 4.95 4.95-1.414 1.414-4.95-4.95-4.95 4.95-1.414-1.414 4.95-4.95-4.95-4.95L7.05 5.636z" fill="currentColor" />
                                            </svg>
                                        </span>
                                    </button>
                                </div>

                            })
                        }

                        {!addTagButton ?
                            <div className="d-flex me-2 mb-2">
                                <input type="text" className="form-control form-control-sm border-0 fw-semibold fs-7" placeholder="Enter Tag..." value={newTag} onChange={this.onChangeTag} onKeyUp={this.onClickNewAddTag} />
                            </div>
                            :
                            <>{!isTrash ? <button type="button" className="btn btn-primary btn-sm rounded-pill me-2 mb-2 py-1" onClick={this.addTagButtonValue} >
                                <span className="svg-icon">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                        <path fill="none" d="M0 0h24v24H0z" />
                                        <path d="M11 11V5h2v6h6v2h-6v6h-2v-6H5v-2z" fill="currentColor" />
                                    </svg>
                                </span>
                                Add Tag
                            </button> : null}</>
                        }
                    </div>
                        :
                        <div className="tags-group d-flex align-items-center flex-wrap">
                            {!addTagButton ?
                                <div className="d-flex me-2 mb-2">
                                    <input type="text" className="form-control form-control-sm border-0 fw-semibold fs-7" placeholder="Enter Tag..." value={newTag} onChange={this.onChangeTag} onKeyUp={this.onClickNewAddTag} />
                                </div>
                                :
                                <>{!isTrash ? <button type="button" className="btn btn-primary btn-sm rounded-pill me-2 mb-2" onClick={this.addTagButtonValue} >
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M11 11V5h2v6h6v2h-6v6h-2v-6H5v-2z" fill="currentColor" />
                                        </svg>
                                    </span>
                                    Add Tag
                                </button> : null}</>
                            }
                        </div>
                    }
                    <h3 className="border-bottom mb-xxl-3 mb-2 pb-xxl-3 pb-2 fs-7 pt-2">
                        <span className="text-uppercase fw-medium">Description</span>
                    </h3>
                    <div className="mb-3 d-flex align-items-center">
                        <div className="d-flex flex-grow-1 pe-2" ref={this.editorEnable}>
                            <textarea type="text" className={`form-control textarea-min-row-3 ${!descriptionEditable ? ' resize-none border-0 p-0' : ''}`}
                                placeholder="Add Description" rows={3} value={content} readOnly={!descriptionEditable} onChange={this.onChangeContent}>

                            </textarea>
                        </div>
                        {!isTrash ? <button type="button" className="btn btn-icon btn-sm btn-active-light w-30px h-30px" ref={this.editorEnableButton}>
                            <span className="svg-icon svg-icon-5 me-0">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                    <g data-name="Group 4328" transform="translate(10956 -4501)">
                                        <rect id="Rectangle_2068" data-name="Rectangle 2068" width="24" height="24" transform="translate(-10956 4501)" fill="none" opacity="0"></rect>
                                        <g id="Group_3906" data-name="Group 3906" transform="translate(-10953.868 4503.559)"><path id="Path_5042" data-name="Path 5042" d="M3.2,14.952,2.3,18.872a.985.985,0,0,0,.956,1.195,1.023,1.023,0,0,0,.206,0L7.4,19.158l7.571-7.543L10.746,7.4Z" transform="translate(0 -2.914)" fill="#545454"></path><path id="Path_5043" data-name="Path 5043" d="M27.5,4.926l-2.82-2.82a.989.989,0,0,0-1.4,0L21.72,3.673l4.221,4.221,1.568-1.568a.989.989,0,0,0,0-1.4Z" transform="translate(-10.152 0)" fill="#545454"></path>
                                        </g>
                                    </g>
                                </svg>
                            </span>
                        </button> : null}
                    </div>
                </div>
                {!isTrash ? <div className="assets_btn_wrapper bg-white">
                    <button type="button" className={`btn btn-primary w-100 ${changesStatus ? `` : `disabled`} `} onClick={this.onSaveChanges}>Save Changes</button>
                </div> : null}
            </PerfectScrollbar>
        </div>
    }
}


const mapStateToProps = (state) => {
    return {
        networkData: state.networks.networkData,
        tagList: state.tags.tagList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        postAddTag: (data) => dispatch(postAddTag(data)),
        postPostUpdateAction: (data, calback) => dispatch(postPostUpdateAction(data, calback)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AssetsModalAside);

